import { MatrixDataService } from './../../services/matrix-data.service';
import { Component, OnInit, Input } from '@angular/core';
import * as FileSaver from 'file-saver';
import { ButtonData } from 'src/app/sites/main/main.component';

@Component({
  selector: 'app-dragndrop',
  templateUrl: './dragndrop.component.html',
  styleUrls: ['./dragndrop.component.scss'],
})
export class DragndropComponent implements OnInit {
  @Input() position: number[] = [];

  files: any[] = [];

  matrixData: ButtonData[][] = [[]];

  constructor(private matrixDataService: MatrixDataService) {}

  ngOnInit() {
    this.matrixDataService.currentData.subscribe((res) => {
      this.matrixData = res;
      console.log(res);
    });
  }
  /**
   * on file drop handler
   */
  onFileDropped($event) {
    this.prepareFilesList($event);
  }

  /**
   * handle file from browsing
   */
  fileBrowseHandler(files) {
    this.prepareFilesList(files);
  }

  /**
   * Delete file from files list
   * @param index (File index)
   */
  deleteFile(index: number) {
    //this.matrixData[this.position[0]][this.position[1]].musicfiles.splice(index, 1);
  }

  /**
   * Simulate the upload process
   */
  uploadFilesSimulator(index: number) {
    setTimeout(() => {
      if (index === this.files.length) {
        return;
      } else {
        const progressInterval = setInterval(() => {
          if (this.files[index].progress === 100) {
            clearInterval(progressInterval);
            this.uploadFilesSimulator(index + 1);
          } else {
            this.files[index].progress += 5;
          }
        }, 200);
      }
    }, 1000);
  }

  /**
   * Convert Files list to normal array list
   * @param files (Files List)
   */
  prepareFilesList(files: Array<any>) {
    console.log(this.position);
    console.log(this.matrixData);
    if (
      this.matrixData[this.position[0]][this.position[1]].action.value == 'MF'
    ) {
      for (const item of files) {
        //item.progress = 0;
        this.matrixData[this.position[0]][this.position[1]].musicfiles.push(
          item
        );
        // FileSaver.saveAs(item, 'test.wav');
      }
    } else {
      this.matrixData[this.position[0]][this.position[1]].musicfiles = [
        files[0],
      ];
    }
  }

  /**
   * format bytes
   * @param bytes (File size in bytes)
   * @param decimals (Decimals point)
   */
  formatBytes(bytes, decimals) {
    if (bytes === 0) {
      return '0 Bytes';
    }
    const k = 1024;
    const dm = decimals <= 0 ? 0 : decimals || 2;
    const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(dm)) + ' ' + sizes[i];
  }
}
